@import url("https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap");
* {
  font-family: "Bree Serif", serif;
}
.quote {
  width: 50%;
  /* margin: auto; */
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  background-color: white;
  color: black;
  padding: 12px;
  border-radius: 5px;
  margin-left: 5rem;
}
.quote > p {
  margin: auto;
  display: block;
}
.randonimage {
  border: 0px solid red;
  margin-top: 130px;
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.randonimage > img {
  width: 100%;
}
#home {
  width: 60%;
  margin: auto;
  margin-top: 130px;
  font-family: serif;

  /* color: #243D25; */
  color: #4c3575;
  border: 0px solid gold;
}
/* style={{ width: "7%", display: "inline",marginTop:"10px" }} */
.home {
  width: 90%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 0px solid green;
  margin: auto;
}
.spanImage {
  width: 7%;
  display: inline;
  margin-top: 10px;
}

#myImg {
  border-radius: 30px;
  width: 40%;
  margin-right: 50px;

  /* height: 450px; */
}

#myDetailsDiv {
  width: 100%;
  margin-left: 60px;
  text-align: justify;
  font-family: "Bree Serif", serif;
  border: 0px solid green;
}
.img_txt_wrapper {
  display: flex;
  border: 2px solid black;
  align-items: center;
  justify-content: space-between;
}
#myName {
  margin-bottom: 7px;
  font-size: 50px;
  animation: type 3s steps(25);
  overflow: hidden;
  border: 0px solid blue;
  font-family: "Bree Serif", serif;
  white-space: nowrap;
}

#title {
  margin-bottom: 30px;
  font-size: 42px;
  animation: type 3s steps(22);
  overflow: hidden;
  font-family: "Bree Serif", serif;
  white-space: nowrap;
  border: 0px solid teal;
}

@keyframes type {
  0% {
    width: 0ch;
  }

  100% {
    width: 22ch;
  }
}

#detail {
  line-height: 26px;
  font-family: serif;
  font-size: 18px;
  border: 0px solid pink;
}
/* <-------Back to top buttun------> */
.bttBtn {
  background-color: rgb(6, 103, 187);
  color: red;
}

.bttBtn:hover {
  background-color: red;
}
/* <-------Back to top buttun------> */
@media screen and (max-width: 1124px) and (min-width: 768px) {
  #home {
    width: 90%;
    margin-top: 150px;
  }
  /* .home{
 
    flex-direction: column;
  } */

  #myDetailsDiv {
    width: 90%;
    margin: auto;
    margin-left: 20px;
  }

  #myName {
    margin-bottom: 10px;
    font-size: 32px;
  }

  #myImg {
    border-radius: 30px;
    width: 50%;
    /* height: 380px; */
    margin: auto;
  }
  .quote {
    width: 80%;
    margin: auto;
  }
  .quote > img {
    margin: auto;
  }
  #title {
    font-size: 20px;
  }

  #detail {
    font-size: 18px;
  }
}

@media screen and (max-width: 767px) and (min-width: 300px) {
  #home {
    width: 90%;
    margin-top: 150px;
  }
  .randonimage {
    border: 0px solid red;
    margin-top: 30px;
    width: 70%;
  }
  #home {
    width: 90%;
  }
  .spanImage {
    width: 12%;
  }
  .home {
    flex-direction: column;
  }
  .img_txt_wrapper {
    margin: auto;
    margin-top: 20px;
    width: 80%;
    flex-direction: column;
  }
  #myDetailsDiv {
    width: 100%;
    margin: auto;
  }
  .quote {
    width: 80%;
    margin: auto;
  }
  #myName {
    margin-bottom: 10px;
    font-size: 25px;
  }

  #myImg {
    border-radius: 30px;
    /* width: 62%; */
    /* height: 380px; */
    margin: auto;
    border: 0px solid red;
  }

  #title {
    font-size: 18px;
  }

  #detail {
    font-size: 18px;
  }
}
